import React, { Component } from 'react';
// import ReactDOM from 'react-dom';
import {Flex,FlexItem,Button} from 'react-weui';
//import styles
import 'weui';
import 'react-weui/build/packages/react-weui.css';
import './map.css'

function appendScript(src, callback) {
    const script = document.createElement("script");

    //
    script.async = false;
    script.onload = callback;
    script.src = src;
    document.body.appendChild(script);
}

function doInitMap() {
    //
    //
    let BMap = window.BMap;
    var map = new BMap.Map("map_container");
    var point = new BMap.Point(116.404, 39.915);
    map.centerAndZoom(point, 15);

    var traffic = new BMap.TrafficLayer();        //       
    // map.addTileLayer(traffic);  

    demoLushu(map);


    return;
}

var lushu;

function demoLushu(map){
        //
    //view-source:http://api.map.baidu.com/library/LuShu/1.2/examples/LuShu.html
    //http://lbsyun.baidu.com/jsdemo.htm#c2_8 
    //http://lbsyun.baidu.com/index.php?title=open/library
    map.enableScrollWheelZoom();
    map.centerAndZoom(new BMap.Point(116.404, 39.915), 13);
    
    // 
    var drv = new BMap.DrivingRoute('', {
        onSearchComplete: function (res) {
            if (drv.getStatus() == BMAP_STATUS_SUCCESS) {
                var arrPois = res.getPlan(0).getRoute(0).getPath();

                var sy = new BMap.Symbol(BMap_Symbol_SHAPE_BACKWARD_OPEN_ARROW, {
                    scale: 0.6,//
                    strokeColor: '#fff',//
                    strokeWeight: '2',//
                });
                var icons = new BMap.IconSequence(sy, '10', '30');
                var polyline = new BMap.Polyline(arrPois, {
                    enableEditing: false,//false
                    enableClicking: true,//true
                    icons: [icons],
                    strokeWeight: '8',//
                    strokeOpacity: 0.8,//0 - 1
                    strokeColor: "#18a45b" //
                });
            
                map.addOverlay(polyline);          //

                // map.addOverlay(new BMap.Polyline(arrPois, { strokeColor: '#111' }));
                map.setViewport(arrPois);

                lushu = new BMapLib.LuShu(map, arrPois, {
                    defaultContent: "",//""
                    autoView: true,//
                    icon: new BMap.Icon('http://api.map.baidu.com/library/LuShu/1.2/examples/car.png', new BMap.Size(52, 26), { anchor: new BMap.Size(27, 13) }),
                    speed: 4500,
                    enableRotation: true,//marker
                    landmarkPois: [
                        { lng: 116.314782, lat: 39.913508, html: '', pauseTime: 2 },
                        { lng: 116.315391, lat: 39.964429, html: '<div><img src="http://map.baidu.com/img/logo-map.gif"/></div>', pauseTime: 3 },
                        { lng: 116.381476, lat: 39.974073, html: '<div><img src="http://ishouji.baidu.com/resource/images/map/show_pic04.gif"/></div>', pauseTime: 2 }
                    ]
                });

                lushu.start();
            }
        }
    });
    drv.search('', '');

}

function initMap() {
    doInitMap();
    return;
    //scriptak
    var url_string = window.location.href;
    var url = new URL(url_string);
    var ak = url.searchParams.get("ak");
    console.log('ak is ' + ak);

    //appenddocument.write,
    //<script type="text/javascript" src="http://api.map.baidu.com/api?v=2.0&ak="></script>
    //
    window.BMap_loadScriptTime = (new Date).getTime();
    //,
    appendScript('http://api.map.baidu.com/api?v=2.0&ak=' + ak, function () {
        //
        //
        doInitMap();
    });
}

const wx = window.wx;
wx.miniProgram.getEnv(function (res) {
    console.log('wx env', res.miniprogram) // true })   
    //postMessage
    wx.miniProgram.postMessage({ data: { foo: 'first message' } });
});

class BaiduMap extends Component {
    componentDidMount() {
        console.log('---loaded..'); // true })    
        initMap();
    }
    onClick(){
        wx.miniProgram.postMessage({ data: { foo: 'bar' } });
        wx.miniProgram.navigateTo({url: '/pages/index/index'})
    }
    onAgainClick(){
        lushu &&  lushu.stop();
        lushu &&  lushu.start();
    }
    render() {
        console.log('---renderes.'); // true })    
        return (
            <div className="element-full-size">
                <div id="map_container"></div>
                <Flex >
                    <FlexItem>
                    <Button size="small" onClick={this.onClick}></Button>
                    </FlexItem>
                    <FlexItem>
                    <Button  size="small" type="default" onClick={this.onAgainClick}></Button>
                    </FlexItem>
                </Flex>    
            </div>
        );
    }
}

export default BaiduMap;




